import BScroll from 'better-scroll'

export default {
  name: 'youxuelist',
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this._setYxListWidth()
        this._initYxList()
      }, 20)
      window.addEventListener('resize', () => {
        if (!this.scroll) {
          return
        }
        setTimeout(() => {
          this._setYxListWidth()
          this.scroll.refresh()
        }, 20)
      })
    })
  },
  methods: {
    showAlert() {
      console.log('父组件开始调用我了,开始刷新页面')
      setTimeout(() => {
        this._setYxListWidth()
        this.scroll.refresh()
      }, 20)
    },
    _setYxListWidth() {
      let width = 0
      let oneWidth = 0
      // let loopString = true
      this.children = this.$refs.tabWrapper.children
      // oneWidth = this.$refs.yxwrapper.getBoundingClientRect().width
      for (let i = 0; i < this.children.length; i++) {
        // let tabNode = this.$refs.tabitem[i]
        let child = this.children[i]
        // child.style.width = oneWidth + 'px'
        // tabNode.style.width = oneWidth + 'px'
        console.log('单个li宽度为：', child.clientWidth, 'marginRight数值是：', getComputedStyle(child).marginRight, ':px')
        width += parseFloat(child.clientWidth) + parseFloat(getComputedStyle(child).marginRight)
      }
      console.log(oneWidth, width)
      /* if (loopString && !isResize) {
        width += 2 * oneWidth
      } */
      this.$refs.tabWrapper.style.width = width + 5 + 'px'
    },
    _initYxList() {
      if (!this.scroll) {
        this.scroll = new BScroll(this.$refs.yx_smalllist, {
          scrollX: true,
          'eventPassthrough': 'vertical' // 横向滚动后会默认阻止纵向滚动，开启
        })
      }
    }
  },
  components: {
    BScroll
  }
}



